</template>
<script>
  import axios from "axios"
  export default{
    data(){
      return {
        equipes:this.$store.state.equipes,
      }
    },
    mounted() {
        axios.get(this.$store.state.url + "/equipe/")
            .then(res => {
              this.equipes = res.data
              console.log(res.data)
            })
            .catch(err => {
                console.error(err); 
            })
        },  
  }
</script>

